@import '~react-agenda/build/styles.css';
@import '~react-datetime/css/react-datetime.css';
@import url('https://fonts.googleapis.com/css?family=Lato:700');
@import url('https://fonts.googleapis.com/css?family=Heebo:900');

.App {
  text-align: center;
  background-color:#181818;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.ant-menu.ant-menu-dark .ant-menu-item-selected, .ant-menu-submenu-popup.ant-menu-dark .ant-menu-item-selected{
  background-color:red!important;
}

.ant-btn-primary{
  background-color:red!important;
  border:none!important;
}
h2, h3{
  color: #fff!important;
  text-align: left;
}
.ant-input{
  background-color:transparent!important;
  border: 1px dashed #fff!important;
  color:#fff!important;
  margin-bottom:10px!important;
}
.ant-table-thead > tr > th{
  background-color:transparent!important;
  border: none;
  color:#fff!important;
  margin-bottom:10px!important;
}
.ant-table table{
  color:#fff;
}
.button{
  background-color:red!important;
  border:none!important;
  color:#fff;
  padding:5px 10px;
  border-radius:10px;
  margin: 5px 5px;
}
.ant-table-thead > tr.ant-table-row-hover > td, .ant-table-tbody > tr.ant-table-row-hover > td, .ant-table-thead > tr:hover > td, .ant-table-tbody > tr:hover > td{
  background-color:#222222!important;
}
.ant-pagination-item-active{
  border-color: red!important;
  color:#fff!important;
  background-color: transparent!important;
}
.ant-pagination-item-active:focus, .ant-pagination-item-active:hover{
  color:#fff!important;
  border-color:red!important;
  background-color: transparent!important;
}
.ant-pagination-disabled a, .ant-pagination-disabled:hover a, .ant-pagination-disabled:focus a, .ant-pagination-disabled .ant-pagination-item-link, .ant-pagination-disabled:hover .ant-pagination-item-link, .ant-pagination-disabled:focus .ant-pagination-item-link{
  color:#fff!important;
  border-color:red!important;
  background-color: transparent!important;
}
.ant-pagination-item-active:focus a, .ant-pagination-item-active:hover a{
  color:#fff!important;
}
.ant-pagination-item-active a{
  color:#fff!important;
}
.ant-select-selection{
  background-color:#222222!important;
  border: 1px dashed #fff!important;
  color:#fff!important;
  margin-bottom:10px!important;
}
.ant-select-dropdown-menu{
  background-color:#222222!important;
}
.ant-select-dropdown-menu-item{
  color:#fff!important;
}
.ant-select-dropdown-menu-item:hover{
  background-color:red!important;
}
.ant-select-dropdown-menu-item-selected, .ant-select-dropdown-menu-item-selected{
  background-color:red!important;
}
button.ant-btn{
  color:#fff!important;
}
.title{
  margin-top:10px;
  color:#fff;
  text-align: left;
  padding-bottom:30px;
}
.title h1{
  color:red;
  font-weight:900;
}
.ant-upload.ant-upload-drag{
  background-color:transparent!important;
  padding:10px;
}
.ant-upload.ant-upload-drag p.ant-upload-text{
  color: #fff!important;
}
.ant-upload.ant-upload-drag p.ant-upload-hint{
  color: #fff!important;
}
.ant-tabs-nav .ant-tabs-tab-active{
  color:red!important;
}
.ant-tabs-nav .ant-tabs-tab:hover{
  color:red!important;
}
.ant-tabs-nav .ant-tabs-tab{
  color:#fff!important;
}
.ant-tabs-ink-bar{
  background-color:red!important;
}

.agenda__cell.\--controls .agenda__prev, .agenda__cell.\--controls .agenda__next{
  background-color:red;
}
.agenda__table.\--header .agenda__cell{
  color:#fff;
}
.agenda__row.\--hour-start>.agenda__cell{
  color:#fff;
}
.agenda-item-description {
  font-size: 10px!important;
  background-color: grey;
}
.agenda__cell:hover{
  background-color:rgba(100,0, 0 , 0.7);
}
.agenda__cell_selected{
  background-color:red;
}
.signup-input{
  border:none!important;
  border-bottom: 1px solid white!important;
  border-radius:0!important;
}

.video-background {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100vw; 
  min-height: 100vh;
}
.login{
  display:flex;
  position: fixed;
  top: 0;
  background: rgba(0, 0, 0, 0.5);
  color: #f1f1f1;
  width: 100%;
  padding: 20px;
}
.login>form{
  display:flex;
justify-content: flex-end;
}
.logo{
  height: 100px;
  border-radius:50%;
  margin: 0px 30px;
}
.home-subtitle{
  font-family: 'Lato', sans-serif;
}
.home-title{
  font-family: 'Heebo', sans-serif;
  color:red!important;
  margin:0;
  font-size:3em;
}
select{
    text-transform: none;
    color: #fff!important;
    background: transparent;
    border-radius: 5px;
    border: 1px dashed #fff;
}
.dragDiv>div{
  position:relative!important;
  background:none!important;
  font-weight:900;
}